<template>
    <div>
        <navi title="" back="true"></navi>
        <div class="recharge-num">
            <p>:</p>
            <input v-model="recharge_num" type="number" style="padding: 0" class="recharge-input" id='rechargeNum'  title="money"/>
        </div>
        <list :items="items" tableviewStyle="mui-table-view-radio"></list>
        <button @click="rechargeAction" class="mui-btn mui-btn-primary bottom-btn"></button>

        <div id="mask" class="mui-backdrop" v-if="showOrder">
            <div class="mask-content">
                <h4><i @click="maskCloseAction" class="mui-icon mui-icon-close close-mask"></i></h4>
                <div v-for="item in orderInfo" class="mui-table-view-cell">
                    <div>{{item}}</div>
                </div>
                <div class="mui-table-view-cell">
                    <div></div>
                    <div class="mui-text-center red">1.</div>
                    <div class="mui-text-center"><img width="35" :src="arrowIcon"></div>
                    <div class="mui-text-center red" >2.</div>
                    <div class="mui-text-center"><img width="35" :src="arrowIcon"></div>
                    <div class="mui-text-center red">3.</div>
                </div>
                <div class="mui-text-center">
                    <button @click="confirmAction" class="mui-btn mui-btn-primary bottom-btn" id="button-buy-offline"></button>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import navi from 'components/navigator.vue'
    import list from 'components/public/baseList.vue'
    import userApi from 'apis/userApi'


    export default {
        data(){
            return {
                arrowIcon:require('assets/mine/wallet/arrow.png'),
                showOrder:false,
                recharge_num:'',
                items:[{
                    title:'',
                    image:require('assets/mine/wallet/pay_nlb.png'),
                    path:'#',
                    style:'mui-selected'
                },{
                    title:'()',
                    image:require('assets/mine/wallet/pay_wx.png'),
                    path:'#'
                },{
                    title:'()',
                    image:require('assets/mine/wallet/pay_alipay.png'),
                    path:'#'
                },{
                    title:'()',
                    image:require('assets/mine/wallet/pay_jd.png'),
                    path:'#'
                },{
                    title:'()',
                    image:require('assets/mine/mine-card.png'),
                    path:'#'
                }],
                orderInfo:[': ',': ',': 9550880202962300165',': 4008081618','QQ: 372150575']
            }
        },
        mounted(){
            //
            mui('.mui-table-view').on('tap','.mui-table-view-cell',function(){
                let title = this.getElementsByClassName('cellTitle')[0].innerHTML
                if (title.trim().startsWith('')){
                    return true
                }else {
                    mui.toast('')
                    return false
                }
            })
        },
        methods:{
            //
            rechargeAction(){
              if (!this.recharge_num || this.recharge_num == 0) {
                  mui.toast('')
                  return
              }
              this.showOrder = true
          },
            //
            confirmAction(){
                mui.confirm('?','',['',''],(value)=>{
                    if (value.index == 1){//
                        return
                    }else {
                        let btn = mui('#button-buy-offline')
                        btn.button('loading')
                        userApi.walletRechargeAction(this.recharge_num)
                                .then(()=>{
                                    mui.toast(',(24)',{
                                        duration:'long',
                                        type:'div'
                                    })
                                    btn.button('reset')
                                    this.$router.back()
                                },err=>{
                                    console.log(err)
                                })
                    }
                })
            },
            maskCloseAction(){
                this.showOrder = false
            }
        },
        components:{
            navi,
            list
        }
    }
</script>

<style scoped>
    .recharge-num {
        background-color: #fff;
        padding: 15px 10px;
    }

    .recharge-num p {
        font-size: 14px;
        text-align: left;
    }

    .recharge-num input {
        border: 0;
        font-size: 30px;
        border-bottom: 1px solid rgba(0,0,0,.2);
    }
    .bottom-btn {
        margin: 0 auto;
        margin-top: 8px;
        margin-bottom: 8px;
        width: 90%;
        height: 44px;
    }
    .mask-content {
        position: relative;
        width: 90%;
        height: 90%;
        margin: 10% auto 0;
        background-color: #fff;
        overflow: scroll;
    }

    .mask-content h4 {
        position: relative;
        text-align: center;
        padding: 10px;
        /*color: #37A061;*/
    }
    .mask-content .mui-table-view-cell {
        font-size: 14px;
        text-align: left;
    }

    .mask-content .red {
        font-weight: bold;
        color:blue;
    }
</style>


