<template>
    <div>
        <navi title="" back="true"></navi>
        <div class="mui-card">
            <div class="mui-card-header">
                <div class="mui-pull-left">
                    <p class="mui-text-left"><span>{{invite[0]}}</span>  <span>{{invite[1]}}</span>  <span>{{invite[2]}}</span></p>
                </div>
            </div>
            <div class="mui-card-content">
                <form class="mui-input-group">
                    <div class="mui-input-row">
                        <label><a class="mui-icon mui-icon-phone"></a></label>
                        <input v-model="form.mobile" type="number" class="mui-input-clear" placeholder="">
                    </div>
                    <div class="mui-input-row">
                        <label><a class="mui-icon mui-icon-chatbubble"></a></label>
                        <input v-model="form.auth_code" type="number" placeholder="">
                    </div>
                    <div class="mui-input-row mui-checkbox mui-left" style="margin-top: 10px; font-size: 14px">
                        <label style="display: inline;line-height: 2"></label><a id="service"  @click="toggleAgreementAction"></a>
                        <input v-model="isAgree" id="is-agree" style="top: 0;" name="is-agree" value="Item 1" type="checkbox">
                    </div>
                    <div class="mui-button-row">
                        <timerButton  class="mui-btn mui-btn-primary codes-btn" :second="30" :btnTapped="sendRegsiterAuthCode"></timerButton>
                        <button @click="sendInviteAction" type="button" class="mui-btn mui-btn-danger reg-btn" :disabled="!isAgree" data-loading-text="..."></button>
                    </div>
                </form>
            </div>
        </div>
        <agreement v-if="showAgreement" :closeAction="toggleAgreementAction"></agreement>
    </div>
</template>

<script>
    import navi from 'components/navigator.vue'
    import agreement from 'components/public/agreement.vue'
    import timerButton from 'components/public/countdownButton.vue'
    import userApi from 'apis/userApi'

    export default {
        data(){
          return {
              invite:['-','-','-'],
              form:{
                  mobile:'',
                  auth_code:''
              },
              isAgree:true,
              showAgreement:false
          }
        },
        mounted(){
          this.fetchData()
        },
        methods:{
            toggleAgreementAction(){
                this.showAgreement = !this.showAgreement
            },
            fetchData(){
              userApi.getMembers()
                      .then(rs=>{
                          this.invite = rs.nums
                      },err=>{
                          console.log(err)
                      })
            },
            sendRegsiterAuthCode(){//
                return userApi.getInvitationAuthCode()
            },
            sendInviteAction(){//
                let {mobile,auth_code} = this.form
                let allValue = mobile && auth_code
                if (!allValue){
                    console.log('')
                    mui.toast('')
                }else {
                    let btn = mui('.reg-btn')
                    btn.button('loading')
                    userApi.invitationUser(this.form)
                            .then(rs=>{
                                mui.toast('')
                                btn.button('reset')
                                this.$router.back()
                            },err=>{
                                btn.button('reset')
                                console.log(err)
                            })
                }

            }
        },
        components:{
            navi,
            agreement,
            timerButton
        }
    }
</script>

<style scoped>
    .mui-card-header span {
        color: #007aff;
    }
</style>


