<template>
    <div class="custom">
        <navi title="" back="true"></navi>

        <img :src="headImage" style="width:100%;">
        <list :items="items"></list>
        <div id="bottomView"></div>
    </div>
</template>

<script>

    import navi from '../navigator.vue'
    import list from '../public/baseList.vue'

    let headImage = require('assets/find/helpList.png')

    export default {
        data(){
            return {
                headImage,
                items:[{
                    title:'',
                    path:{name:'customDetail',query:{type:0,title:''}}
                },{
                    title:'',
                    path:{name:'customDetail',query:{type:1,title:''}}
                },{
                    title:'',
                    path:{name:'customDetail',query:{type:2,title:''}}
                },{
                    title:'',
                    path:{name:'customDetail',query:{type:3,title:''}}
                },{
                    title:'',
                    path:{name:'customDetail',query:{type:4,title:''}}
                },]
            }
        },
        components:{
            navi,
            list,
        }
    }
</script>



