<template>
    <div>
        <navi title="" back="true"></navi>
        <div>
            <div class="header">:{{form.total}}</div>

            <!---->
            <listView :items="items"></listView>

            <!---->
            <listView id="pay_type" :items="payItems" tableviewStyle="mui-table-view-radio"></listView>

            <button @click="payAction" class="mui-btn mui-btn-primary bottom-btn" data-loading-icon="mui-spinner mui-spinner-custom" :disabled="loadData"></button>
            <router-link tag="button" :to="{name:'walletRecharge'}" class="mui-btn mui-btn-danger bottom-btn">?</router-link>

            <!---->
            <div id="mask" class="mui-backdrop" v-if="showOrder">
                <div class="mask-content">
                    <h4><i @click="maskCloseAction" class="mui-icon mui-icon-close close-mask"></i></h4>
                    <div v-for="item in orderInfo" class="mui-table-view-cell">
                        <div>{{item}}</div>
                    </div>
                    <div class="mui-table-view-cell">
                        <div></div>
                        <div class="mui-text-center red">1.</div>
                        <div class="mui-text-center"><img width="35" :src="arrowIcon"></div>
                        <div class="mui-text-center red" >2.</div>
                        <div class="mui-text-center"><img width="35" :src="arrowIcon"></div>
                        <div class="mui-text-center red">3.</div>
                    </div>
                    <div class="mui-text-center">
                        <button @click="confirmAction" class="mui-btn mui-btn-primary bottom-btn" data-loading-icon="mui-spinner mui-spinner-custom"></button>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import navi from 'components/navigator.vue'
    import listView from 'components/public/baseList.vue'
    import homeApi from 'apis/homeApi'

    export default {
        data(){
            return {
                form:{
                    total:'8800',
                    name:'2',
                    time:'5',
                    nums:'1',
                    income:'138'
                },
                loadData:true,
                showOrder:false,//
                arrowIcon:require('assets/mine/wallet/arrow.png'),
                payItems:[{
                    title:'',
                    image:require('assets/mine/wallet/pay_nlb.png'),
                    path:'#',
                    style:'mui-selected'
                },{
                    title:'',
                    image:require('assets/mine/wallet/ye.png'),
                    path:'#'
                },{
                    title:'()',
                    image:require('assets/mine/wallet/pay_wx.png'),
                    path:'#'
                },{
                    title:'()',
                    image:require('assets/mine/wallet/pay_alipay.png'),
                    path:'#'
                },{
                    title:'()',
                    image:require('assets/mine/wallet/pay_jd.png'),
                    path:'#'
                },{
                    title:'()',
                    image:require('assets/mine/mine-card.png'),
                    path:'#'
                }],
                orderInfo:[': ',': ',': 9550880202962300165',': 4008081618','QQ: 372150575']
            }
        },
        mounted(){
            //
            mui('.mui-table-view').on('tap','.mui-table-view-cell',function(){
                let title = this.getElementsByClassName('cellTitle')[0].innerHTML.trim()
                if (title.startsWith('') || title.startsWith('')){
                    return true
                }else {
                    mui.toast('')
                    return false
                }
            })
        },
        activated(){
            let {id,number,order_id} = this.$route.query
            let ratio = number > 10?0.1999:0.1888
            this.form.total = number*8800
            this.form.nums = `${number}`
            this.form.income = `${(number*8800*ratio/12).toFixed(2)}`
            this.fetchData(id)
        },
        computed:{
            items(){//
                let {name,time,nums,income} = this.form
                let list = [name,time,nums,income]
                return [':',':',':',':'].map((item,index)=>{
                    let obj = {}
                    obj.title = item + list[index]
                    obj.path = '#'
                    obj.notShowRight = true
                    return obj
                })
            }
        },
        methods:{
            payAction(){
                let title = document.querySelector('#pay_type .mui-selected .cellTitle').innerHTML.trim()
                let {forest_id,number,order_id} = this.$route.query

                if (title.startsWith('')){//
                    this.showOrder = true
                }else if (title.startsWith('')){//
                    let pay_channel = ''
                    this.postData({forest_id,number,order_id,pay_channel})
                }

            },
            postData(params){//
                let btn = mui('.bottom-btn')
                btn.button('loading')
                homeApi.saveOrder(params)
                        .then(rs=>{
                            mui.toast('')
                            btn.button('reset')
                            this.$router.replace({name:'home'})
                        },err=>{
                            mui.toast('')
                            console.log(err)
                            btn.button('reset')
                        })
            },
            confirmAction(){//
                mui.confirm('?','',['',''],(value)=>{
                    if (value.index == 1){//
                        return
                    }else {
                        let {forest_id,number,order_id} = this.$route.query
                        let pay_channel = ''
                        this.postData({forest_id,number,order_id,pay_channel})
                    }
                })
            },
            maskCloseAction(){//
                this.showOrder = false
            },
            fetchData(id){//
                homeApi.getTreeDetail(id)
                        .then(rs=>{
                            this.loadData = false

                            let [,data,userinfo] = rs
                            this.form.name = data.areaName
                            let capital = userinfo.capital
                            this.payItems[1].title = ` (${capital})`

                        },err=>{
                            this.loadData = false
                            console.log(err)
                        })
            }
        },
        components:{
            navi,
            listView
        }
    }
</script>

<style scoped>
    .bottom-btn {
        margin: 0 auto;
        margin-top: 8px;
        margin-bottom: 8px;
        width: 90%;
        height: 44px;
    }
    .mask-content {
        position: relative;
        width: 90%;
        height: 90%;
        margin: 10% auto 0;
        background-color: #fff;
        overflow: scroll;
    }

    .mask-content h4 {
        position: relative;
        text-align: center;
        padding: 10px;
        /*color: #37A061;*/
    }
    .mask-content .mui-table-view-cell {
        font-size: 14px;
        text-align: left;
    }

    .mask-content .red {
        font-weight: bold;
        color:blue;
    }
</style>


