<template>
    <div>
        <navi title="" back="true"></navi>

        <h5 class="mui-content-padded"></h5>

        <form class="mui-input-group">
            <div class="mui-input-row">
                <label class="nlb-modify-lable"></label>
                <select v-model="info.province_id" @change="getCity" id="province" type="text" class="mui-input-clear nlb-modify-input" placeholder="">
                    <option value="0">{{pre_text[0]}}</option>
                    <option v-for="item in info.provinceList" :value="item.id">{{item.name}}</option>
                </select>
            </div>
            <div class="mui-input-row">
                <label class="nlb-modify-lable"></label>
                <select v-model="info.city_id"   @change="getCountry" id="city" type="text" class="mui-input-clear nlb-modify-input" placeholder="">
                    <option value="0">{{pre_text[1]}}</option>
                    <option v-for="item in info.cityList" :value="item.id">{{item.name}}</option>
                </select>
            </div>
            <div class="mui-input-row">
                <label class="nlb-modify-lable"></label>
                <select v-model="info.country_id" id="country" type="text" class="mui-input-clear nlb-modify-input" placeholder="/">
                    <option value="0">{{pre_text[2]}}</option>
                    <option v-for="item in info.countryList" :value="item.id">{{item.name}}</option>
                </select>
            </div>
            <div class="mui-input-row" style="height: auto!important;">
                <textarea v-model="info.address" rows="3" cols="20" placeholder=""></textarea>
            </div>
        </form>

        <button @click="appendAddress" class="mui-btn mui-btn-primary bottom-btn"></button>
    </div>

</template>

<script>
    import navi from 'components/navigator.vue'
    import userApi from 'apis/userApi'

    export default {
        data(){
            return {
                pre_text:['/','',''],
                info:{
                    province_id:0,
                    provinceList:[],
                    city_id:0,
                    cityList:[],
                    country_id:0,
                    countryList:[],
                    address:''
                }
            }
        },
        mounted(){
            this.getProvince()
        },
        methods:{
            changeArea(pid){
                return userApi.getAreaList(pid)
            },
            showInfo(err){
                mui.toast(''+err)
            },
            getProvince(){//,
              this.changeArea(0).then(rs=>{
                  this.info.provinceList = rs.list
              },this.showInfo)
            },
            getCity(){//,
                this.pre_text = []
                this.changeArea(this.info.province_id).then(rs=>{
                    this.info.cityList = rs.list
                },this.showInfo)
            },
            getCountry(){//,
                this.changeArea(this.info.city_id).then(rs=>{
                    this.info.countryList = rs.list
                },this.showInfo)
            },
            appendAddress(){
                let {province_id,city_id,country_id,address} = this.info

                let allValue = province_id && city_id && country_id && address
                if (!allValue){
                    mui.toast('')
                }else {
                    let provinceSelect = document.getElementById('province')
                    let citySelect = document.getElementById('city')
                    let countrySelect = document.getElementById('country')

                    let province = provinceSelect.options[provinceSelect.selectedIndex].innerHTML
                    let city = citySelect.options[citySelect.selectedIndex].innerHTML
                    let country = countrySelect.options[countrySelect.selectedIndex].innerHTML

                    let btn = mui('.bottom-btn')
                    btn.button('loading')
                    userApi.updateAdress({
                        province,
                        city,
                        country,
                        province_id,
                        city_id,
                        country_id,
                        address
                    }).then((rs)=>{
                        btn.button('reset')
                        mui.toast('')
                        this.$router.back()
                    },err=>{
                        console.log(err)
                    })
                }

            }
        },
        components:{
            navi
        }
    }
</script>

<style scoped>
    .bottom-btn{
        padding: 6px 12px;
        margin: 15px auto;
        width: 90%;
        line-height: 32px;
    }
</style>


