<template>
    <div class="p-mgn0" style="text-align:center;">
        <navi title="" back="true"></navi>

        <h5 class="mui-content-padded"></h5>

        <form class="mui-input-group">
            <div class="mui-input-row">
                <label></label>
                <input v-model="info.name" type="text" class="mui-input-clear" placeholder="">
            </div>
            <div class="mui-input-row">
                <label></label>
                <input v-model="info.id" type="text" class="mui-input-clear" placeholder="">
            </div>
            <div class="mui-input-row">
                <label></label>
                <input v-model="info.bank_name" type="text" class="mui-input-clear" placeholder="">
            </div>
            <div class="mui-input-row">
                <label></label>
                <input v-model="info.card_no" type="text" class="mui-input-clear" placeholder="">
            </div>
            <div class="mui-input-row">
                <label></label>
                <input v-model="info.auth_code" type="number" placeholder="">
            </div>
            <div class="mui-btn-row">
                <timerButton class="mui-btn mui-btn-primary codes-btn-sms-login" :second="30" :btnTapped="sendAuthCode"></timerButton>
                <button @click="bindAction" type="button" class="mui-btn mui-btn-danger bind-btn" data-loading-icon="mui-spinner mui-spinner-custom" :disabled="!info.isAgree"></button>
            </div>
        </form>

        <div class="mui-input-row mui-checkbox mui-left agreement">
            <label for="is-agree"></label><a @click="toggleAgreementAction"></a>
            <input v-model="info.isAgree" id="is-agree" name="is-agree" value="Item 1" type="checkbox">
        </div>

        <agreement v-if="showAgreement" :closeAction="toggleAgreementAction"></agreement>
    </div>
</template>

<script>
    import navi from 'components/navigator.vue'
    import timerButton from 'components/public/countdownButton.vue'
    import agreement from 'components/public/agreement.vue'

    import userApi from 'apis/userApi'
    export default {
        data(){
            return {
                info:{
                    name:'',
                    id:'',
                    bank_name:'',
                    card_no:'',
                    auth_code:'',
                    isAgree:true
                },
                showAgreement:false
            }
        },
        mounted(){
          userApi.getUserInfo().then(({name,id,card_no,bank_name})=>{
              let info = this.info
              info.name = name
              info.id = id
              info.card_no = card_no
              info.bank_name = bank_name
          },err=>{
              console.log(err)
          })
        },
        methods:{
            bindAction(){//
                let data = this.info
                let hasAuthCode = data.auth_code
                let allValue = data.name && data.id && data.bank_name && data.card_no

                if (!allValue){
                    mui.toast('')
                }else if(!hasAuthCode){
                    mui.toast('')
                }else {
                    let btn = mui('.bind-btn')
                    btn.button('loading')
                    userApi.bindCard(this.info)
                            .then(rs=>{
                                btn.button('reset')
                                mui.toast('')
                                //
                                this.$router.back()
                            },err=>{
                                console.error(err)
                            })
                }
            },
            sendAuthCode(){
                return userApi.sendBindingCardAuthCode()
            },
            toggleAgreementAction(){
                this.showAgreement = !this.showAgreement
            }
        },
        components:{
            navi,
            timerButton,
            agreement
        }
    }
</script>

<style scoped>
    label {
        font-size: 14px;
    }
    .mui-btn-row button {
        margin-top: 8px;
        margin-bottom: 8px;
    }
    .agreement {
        margin: 0 auto;
    }
    .mui-checkbox {
        margin-top: 10px;
        font-size: 14px;
        text-align: left;
    }
    .mui-checkbox label{
        display: inline;
        line-height: 2;
    }
    .mui-checkbox input {
        top: 0;
    }
</style>


